::-webkit-scrollbar {
  height: 8px;
  width: 9px;
  background: #f5f6fa;
  border-radius: 6px;
}
::-webkit-scrollbar-thumb {
  background: #e7ecf3;
  border-radius: 6px;
}

/* Analytics Components */
.analytics-card {
  background: white;
  border-radius: 1rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
}

.analytics-card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.analytics-title {
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
}

/* Recording Player */
.recording-player {
  background: white;
  border-radius: 1rem;
  overflow: hidden;
}

.player-controls {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
}

.progress-bar {
  flex: 1;
  height: 4px;
  background: #e5e7eb;
  border-radius: 2px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  background: #2563eb;
  transition: width 0.1s linear;
}

.player-time {
  font-size: 0.75rem;
  color: #6b7280;
}

/* Summary Card */
.summary-card {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.summary-content {
  flex: 1;
  padding: 1rem;
}

.summary-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin: 0.5rem 0;
}

.summary-tag {
  padding: 0.25rem 0.75rem;
  background: #eff6ff;
  color: #2563eb;
  border-radius: 9999px;
  font-size: 0.75rem;
  font-weight: 500;
}

/* Charts */
.chart-container {
  padding: 1rem;
  background: white;
  border-radius: 1rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
}

.chart-header {
  margin-bottom: 1rem;
}

.chart-title {
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
}

.chart-subtitle {
  font-size: 0.875rem;
  color: #6b7280;
  margin-top: 0.25rem;
}

/* Misc UI utility classes */
.truncate {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 140px;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .app-main {
    padding: 1rem;
  }
  
  .grid-cols-4 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 768px) {
  .nav-actions {
    display: none;
  }
  
  .grid-cols-2 {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
  
  .table-filters {
    flex-direction: column;
  }
  
  .analytics-card {
    padding: 1rem;
  }
  
  .stat-card, .data-table, .table { 
    font-size: 0.97em; 
  }
  
  .stat-number { 
    font-size: 1.3rem;
  }
}

/* Utility Classes */
.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.gap-2 {
  gap: 0.5rem;
}

.gap-4 {
  gap: 1rem;
}

.text-sm {
  font-size: 0.875rem;
}

.font-medium {
  font-weight: 500;
}

.text-gray-500 {
  color: #6b7280;
}

.rounded-full {
  border-radius: 9999px;
}

.shadow-sm {
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
}

.transition-all {
  transition: all 0.2s;
}
